*,
:after,
:before {
  box-sizing: border-box;
}

::selection {
  background: #2a9d8f;
}

body,
html {
  padding: 0;
  margin: 0;
  width: 100%;
  font-weight: 300;
  font-family: Apercu, "Source Sans Pro", system, system-ui, -apple-system,
    BlinkMacSystemFont, Roboto, Helvetica, Arial, sans-serif;
  background-color: #264653;
  color: #ffffff;
}

/* HEADER STYLING*/
.header {
  width: 200px;
  margin-right: 70px;
  letter-spacing: 1.5px;
  font-weight: 700;
  color: #e9c46a;
  text-transform: uppercase;
  text-align: right;
  flex-shrink: 0;
}

@media screen and (max-width: 1024px) {
  .header {
    margin-right: 50px;
  }
}

@media screen and (max-width: 850px) {
  .header {
    width: 150px;
  }
}

@media screen and (max-width: 768px) {
  .header {
    text-align: left;
    width: auto;
  }
}

/* SECTION STYLING */
section {
  display: flex;
  width: 100vw;
  margin-bottom: 25px;
  justify-content: center;
  padding: 100px 170px;
}

@media screen and (max-width: 1280px) {
  .section {
    padding: 100px;
  }
}

@media screen and (max-width: 1024px) {
  .section {
    padding: 50px;
  }
}

@media screen and (max-width: 768px) {
  .section {
    display: block;
  }
}

@media screen and (max-width: 480px) {
  .section {
    padding: 50px 35px;
  }
}

/* CONTENT STYLING */
.content {
  font-weight: 300;
  font-size: 1rem;
  width: 100%;
  max-width: 650px;
}

.section__content p {
  margin-top: 0;
  margin-bottom: 25px;
}

@media screen and (max-width: 768px) {
  .content {
    padding-top: 30px;
    padding-left: 50px;
  }
}

@media screen and (max-width: 480px) {
  .content {
    padding-left: 30px;
  }
}

/* LINKS */
a {
  color: #ffffff;
  transition: all 0.1s ease-in-out;
  text-decoration: none;
  font-weight: 700;
}

a:hover {
  background-color: #2a9d8f;
}
